

import {
    
    USERS_LENGTH_LOADING,
    USERS_LENGTH_LOADED

} from "../action/types";

      const initialState = {
        //  token:localStorage.getItem("token"),
        //  isAuthenticated:null,
        isLoading: false,
        userLength:0,
      };
      
      export default function (state = initialState, action) {
        switch (action.type) {
          case USERS_LENGTH_LOADED:
            // console.log("ACTION PAYLOAD", action.payload);
            return {
              ...state,
              // isAuthenticated:true,
              isLoading: false,
              userLength:action.payload
            };
            case USERS_LENGTH_LOADING:
                return {
                    isLoading: true,
                };      
            default:
                return state;
        }
      }
      