// GET A CONTRIBUTORS FOR A PARTICULAR PROJECT

let notifier = new AWN
export const getContributors = (id) => dispatch => {
    dispatch({
      type:  CONTRIBUTORS_LOADING
    });

    console.log("id ,", id)
  
    axios 
      .get(`/api/projects/${id}/contributors`)
      .then(res =>{
        console.log(
          'contributors ', res.data
        );

        dispatch({
          type: CONTRIBUTORS_LOADED,
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        // dispatch(
        //   returnErrors(err.response.data, err.response.status, "REGISTER_FAIL")
        // );
        console.log(err);
        
      notifier.warning("Something went wrong")
        // dispatch({
        //   type: CREATE_CAMPAIGN_FAILED
        // });
      });
  };




