export const getTotalContributors = (id) => dispatch => {
  dispatch({
    type:  CONTRIBUTORS_LENGTH_LOADING
  });

  axios 
    .get(`/api/projects/${id}/contributors/length`)
    .then(res =>{

      dispatch({
        type: CONTRIBUTORS_LENGTH_LOADED,
        payload: res.data
      })
      // console.log(res.data)
    }
    )
    .catch(err => {
      // dispatch(
      //   returnErrors(err.response.data, err.response.status, "REGISTER_FAIL")
      // );

      notifier.warning("Something went wrong")
      console.log(err);
      // dispatch({
      //   type: CREATE_CAMPAIGN_FAILED
      // });
    });
};

