export const getUserBadge= (email) => dispatch => {
    dispatch({
      type:  LOADING_USER_BADGE
    });    
    axios
      .get(`api/badge/${email}/badges`)
      .then(res =>{
        dispatch({
          type: LOADED_USER_BADGE,
          payload: res.data
        })
        console.log(res.data)
      }
      )
      .catch(err => {
        console.log("ERR",err.response.data);
      });
  };
