import React, { Component } from 'react';
import Fragment from "../../hoc/Fragment"
import Header from "../home/components/Header"
import Footer from "../home2/components/Footer"
import List from "./components/List"
import Single from "./components/Single"
import Category from "./components/Category"
import Fund from "./components/Fund"
import FundLogin from "./components/Login"
import Demo from "./components/Demo"
import Video from "./components/Video"
// import Main from "./Main"
import RecentCampaigns from "./components/RecentCampaigns"
import { 
    Link,
    Route,
    Switch, 
    BrowserRouter as Router } from "react-router-dom"
import {Helmet} from 'react-helmet'
import NotFound from '../404/404';
import CampaignCategory from './components/CampaignCategory';

class Index extends Component {
    render() {
        return (
                <Fragment>
            <Helmet>
                <title>Explore Projects On Elesarr</title>
                        <meta 
                            name="description" 
                            content="Elesarr connects you to the project you care about and gives you the opportunity to be part of it. Explore projects now!!"
                        />

                        <meta 
                            name="keywords" 
                        content={`
                                Nigeria's crowdfunding platform, 
                                Crowdfunding platform in Nigeria,
                                nigeria,
                                blockchain,
                                elesarr, 
                                crowdfunding in Nigeria, 
                                online platforms,
                                blockchain crowdfunding platforms,
                                choose elesarr,
                                elesarr crowdfunding platform,
                                ethereum in Nigeria,
                                Choose Elesarr today,
                                Get your project out there now on elesarr
                                creatives in nigeria funds,
                                small scale enterprenuers in nigeria funds
                                `}
                        />

            </Helmet>
            <Header />                 
            <Switch>
                <Route exact path="/campaigns/">
                    <List />
                </Route>
                <Route exact path="/campaigns/:id" component={Single} />
                <Route  path="/campaigns/:id/fund" component={Fund} />
                <Route path="/campaigns/:id/login" component={FundLogin} />
                <Route  path="/campaigns/categories/:slug" component={CampaignCategory} />
                <Route path="/campaigns/:id/video" component={Video} />
                <Route component={NotFound} /> 
            </Switch>           
            <Footer />
            </Fragment>
        );
    }
}


  
  // export default ProjectForm
  export default Index