let notifier = new AWN



export const getPosts= () => dispatch => {
    dispatch({
      type:  POST_LOADING
    });
    
  
    
    axios
      .get(`api/blog/posts`)
      .then(res =>{
        dispatch({
          type: POST_LOADED,
          payload: res.data
        })
        console.log(res.data)
      }
      )
      .catch(err => {
        console.log(err);
      });
  };

  

