  export const getSinglePost= (slug) => dispatch => {
    dispatch({
      type:  SINGLEPOST_LOADING
    });
    

    
    axios
      .get(`api/blog/posts/${slug}`)
      .then(res =>{
        dispatch({
          type: SINGLEPOST_LOADED,
          payload: res.data
        })
      }
      )
      .catch(err => {
        // console.log(err.response.data);
        console.log(err.msg);
      });
  };

  export const createWriterPost = (fd) => (dispatch) => {
    const config = {
      headers: {
        // 'Accept': 'application/json',
        "x-auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json"
      }
    };
    dispatch({
      type: WRITER_CREATING_POST,
    });

    new AWN().asyncBlock(
    axios
      .post(`/api/blog/writer-create-posts`, fd, config)
      .then((res) => {
        // alert("ORGANISATION FLAGED SUCCESSFULLY")
        dispatch({
          type: WRITER_CREATED_POST,
        });
  
        // window.location.href = "/admin/dashboard";
        setTimeout(() => {
            window.location.href = ".";
        }, 3000);
      }), "Post Added Successfully")
      .catch((err) => {
        // alert("CREATE POST FALIED")
        notifier.warning("SOMETHING WENT WRONG, CONTACT ADMINISTRATOR OR EMAIL CUSTOMERCARE@ELESARR.COM")
        console.log("error !!!!!!!!!!!!!" ,err.response.data);
      });
  };
  

  

  export const createComment = (slug,{content, post, owner}) => dispatch => {
    const config = {
      headers: {
    // 'Accept': 'application/json',
    'Content-Type': 'application/json'
  },
  };
    const body = JSON.stringify({content, post, owner});
    dispatch({
      type:  CREATING_COMMENT
    });
    axios
      .post(`/api/blog/post/${slug}/comment`, body, config)
      .then(res =>{
        // alert("ORGANISATION FLAGED SUCCESSFULLY")
        dispatch({
          type:   CREATED_COMMENT
        })
        // window.location.href= "."
        window.location.reload()
        notifier.success("Commented Successfully")
      }
      )
      .catch(err => {
        // alert("CREATE POST FALIED")
        // console.log(err.response.data)
        console.log( "error",  err.response.data);
      });
  };


  