// GET A USERS FOR A PARTICULAR PROJECT
export const getPayoutRequests = () => dispatch => {
    dispatch({
      type:  PAYOUT_LOADING
    });

    const config = {
      headers: {
        // 'Accept': 'application/json',
        "x-auth-token": localStorage.getItem("token"),
        'Content-Type': 'application/json',
      }
    };
  
    axios
      .get(`/api/payout`, config)
      .then(res =>{
        dispatch({
          type:PAYOUT_LOADED,
          
          payload: res.data
        })
        console.log(res.data)
      }
      )
      .catch(err => {
        // console.log(err);
        notifier.warning("Something went wrong. Please contact the elesarr team via the customer chat at the bottom right of your screen")
        notifier.warning(" or contact us at customercare@elesarr.com")
        console.log("ERR", err.response.data)
      });
  };

  export const getTotalPayoutRequests = () => dispatch => {
    dispatch({
      type:  PAYOUT_LENGTH_LOADING
    });
  
    axios
      .get(`/api/payout/requests/length`)
      .then(res =>{
        dispatch({
          type:PAYOUT_LENGTH_LOADED,
          
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        // console.log(err);
        console.log("ERR")
      });
  };

  
  export const addPayoutRequests = ({
      email, 
      projectID,
      accountNO,
      bankName

    }) => dispatch => {
    
      const config = {
        headers: {
          // 'Accept': 'application/json',
          "X-auth-token": localStorage.getItem("token"),
          'Content-Type': 'application/json',
        }
      };
      // Request body
      const body = JSON.stringify({
        email, 
      projectID,
      accountNO,
      bankName
    });
    
    dispatch({
      type:  PAYOUT_CREATING_LOADING
    });
    console.log(body)
  // console.log(body['email'])

    axios
      .post(`/api/payout/${projectID}`,body, config)
      .then(res =>{
        dispatch({
          type:PAYOUT_CREATED_LOADED,
          
          payload: res.data
        })
        // console.log(res.data)
        window.location.href = "/dashboard"
      }
      )
      .catch(err => {
        // console.log(err);
        console.log(err.response.data)
        notifier.warning("Something went wrong. Please contact the elesarr team via the customer chat at the bottom right of your screen")
        notifier.warning(" or contact us at customercare@elesarr.com")
      });

  };
