export const addNewsletterUser = ({email}) => dispatch => {
    const config = {
      headers: {
    // 'Accept': 'application/json',
    'Content-Type': 'application/json'
  },
  };
    const body = JSON.stringify({email});
    dispatch({
      type:  ADDING_USER_NEWSLETTER
    });
    // ,
    

    axios
      .post(`/api/newsletter`, body, config)
      .then(res =>{
        // alert("ORGANISATION FLAGED SUCCESSFULLY")
        dispatch({
          type:   ADDED_USER_NEWSLETTER
        })
        window.location.href= "."
      }
      )
      .catch(err => {
        // alert("CREATE POST FALIED")
        // console.log(err.response.data)
        dispatch(returnErrors(err.response.data, err.response.status, "ADDED_USER_NEWSLETTER_FAILED"))
        notifier.warning(err.msg)
        console.log(err.response.data)
      
      });
  };


