import {
    SETTING_WRITER,
    SET_WRITER,
    WRITER_POST_LOADING,
WRITER_POST_LOADED,
WRITER_EDITED_POST,
WRITER_EDITING_POST,
WRITER_SINGLE_POST_LOADING,
WRITER_SINGLE_POST_LOADED,
WRITER_DELETING_POST,
WRITER_DELETED_POST,
} from "../action/types";

      const initialState = {
        //  token:localStorage.getItem("token"),
        //  isAuthenticated:null,
        isLoading: false,
        writer:null,
        posts:[],
        singlePost:null,
        deletedPost:null
      };
      
      export default function (state = initialState, action) {
        switch (action.type) {
          case SET_WRITER:
            // console.log("ACTION PAYLOAD", action.payload);
            return {
              ...state,
              // isAuthenticated:true,
              isLoading: false,
              writer:action.payload
            };
            case SETTING_WRITER:
                return {
                    isLoading: true,
                };
            
                


                case WRITER_POST_LOADED:
                    // console.log("ACTION PAYLOAD", action.payload);
                    return {
                      ...state,
                      // isAuthenticated:true,
                      isLoading: false,
                      posts:action.payload
                    };
                case WRITER_POST_LOADING:
                    return {
                        isLoading: true,
                    };
                
                 
                        
                case WRITER_EDITED_POST:
                    // console.log("ACTION PAYLOAD", action.payload);
                    return {
                        ...state,
                        // isAuthenticated:true,
                        isLoading: false,
                        posts:action.payload
                    };
                case WRITER_EDITING_POST:
                    return {
                        isLoading: true,
                    };
                            
                         

                    case WRITER_SINGLE_POST_LOADED:
                        // console.log("ACTION PAYLOAD", action.payload);
                        return {
                            ...state,
                            // isAuthenticated:true,
                            isLoading: false,
                            singlePost:action.payload
                        };
                    case WRITER_SINGLE_POST_LOADING:
                        return {
                            isLoading: true,
                        };
                                
                             
    
    
                        
                        case WRITER_DELETED_POST:
                            // console.log("ACTION PAYLOAD", action.payload);
                            return {
                                ...state,
                                // isAuthenticated:true,
                                isLoading: false,
                                deletedPost:action.payload
                            };
                        case WRITER_DELETING_POST:
                            return {
                                isLoading: true,
                            };
                                    


                                 
        

            default:
                return state;
        }
      }
      
