/** CREATE CAMPAIGN  */
export const createCampaign = (fd) => dispatch => {  
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token" : localStorage.getItem('token'),
      'Content-Type': 'multipart/form-data',
    }
  };
  new AWN().asyncBlock(
  axios
    .post("/api/projects/create-campaign",fd,config)
    .then(res =>{
      dispatch({
        type: CAMPAIGN_CREATED,
        payload: res.data
      })

      // console.log("data", res.data)

      setTimeout(() => {
        window.location.href =`/dashboard/`
      }, 3000);

      setTimeout(() => {
        
        notifier.info("You campaign has been saved, it will take two to three business days for your campaign to be approved")
        }, 1000);
    }),
    'Campaign has been created successfully, redirecting back to your homepage ...',
  )
    .catch(err => {
      // console.log("error ", err.response.data)
      dispatch(returnErrors(err.response.data, err.response.status, "CREATE_CAMPAIGN_FAILED"))
      notifier.warning("Something went wrong")


    });
};

