/** UPDATE CAMPAIGN */
export const updateCampaign = ({
  userID,
  
  project_name,
  project_description, 
  fb_url,
  linkedin,
  inst_url,
  video_url,
  category,
  id
}) => dispatch => {  
    // Headers
    const config = {
      headers: {
        // 'Accept': 'application/json',
        "x-auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json"
      }
    };
  const body = JSON.stringify({
    userID,
    project_name,
    project_description, 
    fb_url,
    linkedin,
    inst_url,
    video_url,
    category
  })

  // const userID=fd.get("userID")
  new AWN().async(
  axios
    .put(
      `/api/projects/campaign/update/${id}`,
    body,
      config
    )
    .then(res =>{
      dispatch({
        type: CAMPAIGN_UPDATED,
        payload: res.data
      })
      // console.log("RESPONSE", res.data)

      // window.location.href=`/dashboard`

      setTimeout(() => {
        // window.location.reload()
        
      window.location.href=`/dashboard/projects/${userID}`
      }, 3000);
    }), "Campaign Was Updated Successfully")
    .catch(err => {
      // dispatch(
      //   returnErrors(err.response.data, err.response.status, "CREATE CAMPAIGN FAILED")
      // );
      dispatch(
        returnErrors(err.response.data, err.response.status, "CREATE_CAMPAIGN_FAILED")
    )

    notifier.warning("Something went wrong. Please refresh and try again")
    // console.log(err)
    // console.log(err)
    console.clear()
      // console.log(err.response.data);
      // dispatch({
      //   type: CREATE_CAMPAIGN_FAILED
      // });

    });
};












