  /** UPDATES ON CAMPAIGN */
  
  export const writeCampaignUpdate = ({
    userID,
    projectID, 
    content
  }, slug) => dispatch => {  
    const config ={
      headers:{
          'Content-Type':'application/json'
      }
  };
  
    const body = JSON.stringify({
      userID,
      projectID, 
      content
    })
    
  
  
    axios
      .put(
        `/api/projects/${slug}/update`,
      body,
        config
      )
      .then(res =>{
        dispatch({
          type: UPDATE_CREATED,
          payload: res.data
        })
        window.location.href=`/campaign/${slug}`
      })
      .catch(err => {
        dispatch(
          returnErrors(err.response.data, err.response.status, "CREATE_CAMPAIGN_FAILED")
      )
      });
  };
  


