// =================================ADD FAQ===========================================


export const addFaq = (slug,{question , answer, project}) => dispatch => {
  // console.log("content ", question,answer,project)
  const config = {
    headers: {
  // 'Accept': 'application/json',
  "x-auth-token" : localStorage.getItem('token'),
  'Content-Type': 'application/json'
},
};
  const body = JSON.stringify({question , answer, project});
  dispatch({
    type:  ADDING_FAQ
  });
  axios
    .post(`/api/projects/${slug}/faq`, body, config)
    .then(res =>{
      // alert("ORGANISATION FLAGED SUCCESSFULLY")
      dispatch({
        type:   ADDED_FAQ,
        payload : res.data
      })

      notifier.success("FAQ was added successfully")


      setTimeout(() => {
        
      notifier.info("You can add more FAQ's or leave this page if you are done.")
      }, 1000);


      if(res.data.faq.length <3){
        setTimeout(() => {
          notifier.info("You should have a minimum of 3 FAQs")
  
        }, 2500);
      }else{
        setTimeout(() => {
          notifier.info("You have above 3 FAQs which should be enough. You can add more if you wish")
  
        }, 2500);
      }

    


      // console.log("res ", res)

  
      // window.location.reload()

      // getSingleCampaign(slug)



    }
    )
    .catch(err => {
      // alert("CREATE POST FALIED")
      // console.log(err.response.data)
      // console.log(err.response.data);
      notifier.warning("Something went wrong. Please refresh and try again")
    });
};



