import {
    
    LOADING_USER_BADGE,
    LOADED_USER_BADGE

} from "../action/types";

      const initialState = {
        //  token:localStorage.getItem("token"),
        //  isAuthenticated:null,
        isLoading: false,
        badges:[],
      };
      
      export default function (state = initialState, action) {
        switch (action.type) {
          case LOADED_USER_BADGE:
            // console.log("ACTION PAYLOAD", action.payload);
            return {
              ...state,
              // isAuthenticated:true,
              isLoading: false,
              badges:action.payload
            };
            case LOADING_USER_BADGE:
                return {
                    isLoading: true,
                };      
            default:
                return state;
        }
      }
      