// GET A USERS FOR A PARTICULAR PROJECT
export const getUsers = (id) => (dispatch) => {
  dispatch({
    type: ADMIN_USERS_LOADING,
  });

  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };

  axios
    .get(`/api/admin/users`, config)
    .then((res) => {
      dispatch({
        type: ADMIN_USERS_LOADED,
        payload: res.data,
      });
      // console.log(res.data)
    })
    .catch((err) => {
      console.log(err);
    });
};

