/** FLAG FUNCTIONALITY */

export const flagUser = ({ adminId, userID }) => (dispatch) => {
  dispatch({
    type: ADMIN_FLAGING_USER,
  });
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };
  axios
    .put(`/api/admin/${adminId}/flag/${userID}`, config)
    .then((res) => {
      alert("USER FLAGED SUCCESSFULLY");
      dispatch({
        type: ADMIN_FLAGED_USER,
      });
      window.location.href = ".";
    })
    .catch((err) => {
      alert("Flag Failed");
      console.log(err.response.data);
    });
};

/** VERIFY CAMPAIGN FUNCTIONALITY */

export const verifyCampaign = (slug) => (dispatch) => {
  // dispatch({
  //   type:  ADMIN_FLAGING_USER
  // });
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };
  // console.log(localStorage.getItem("token"))
  new AWN()
    .asyncBlock(
      axios.put(`/api/admin/get-project/${slug}/publish`,{}, config).then((res) => {
        // alert("USER FLAGED SUCCESSFULLY");
        // dispatch({
        //   type: ADMIN_FLAGED_USER,
        // });
        // window.location.href = ".";

        setTimeout(() => {
          window.location.reload()
        }, 3000)
      }, "Campaign Was Published Successfully"),

     
    )
    .catch((err) => {
      // alert("Flag Failed");
      notifier.warning("Verification Failed. Please refresh and try again")
      console.log(err.response.data);
    });
};



