export const createPost = (fd) => (dispatch) => {
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };
  dispatch({
    type: ADMIN_CREATING_POST,
  });

  new AWN().asyncBlock(
  axios
    .post(`/api/blog/posts`, fd, config)
    .then((res) => {
      // alert("ORGANISATION FLAGED SUCCESSFULLY")
      dispatch({
        type: ADMIN_CREATED_POST,
      });

      // window.location.href = "/admin/dashboard";
      setTimeout(() => {
          window.location.href = "/admin/dashboard";
      }, 3000);
    }), "Post Added Successfully")
    .catch((err) => {
      // alert("CREATE POST FALIED")
      notifier.warning("Something went wrong")


      console.log("error !!!!!!!!!!!!!" ,err.response.data);
    });
};




export const createPostCategory = ({category_name}) => (dispatch) => {
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };

  const body = JSON.stringify({category_name});

  dispatch({
    type: ADMIN_CREATING_POST_CATEGORY,
  });
  axios
    .post(`/api/admin/create-blog-category`, body, config)
    .then((res) => {
      // alert("ORGANISATION FLAGED SUCCESSFULLY")
      dispatch({
        type: ADMIN_CREATED_POST_CATEGORY,
      });
      window.location.href = "/admin/dashboard";
    })
    .catch((err) => {
      // alert("CREATE POST FALIED")
      // console.log(err.response.data);
      dispatch(returnErrors(err.response.data, err.response.status, "ADDED_USER_NEWSLETTER_FAILED"))
      notifier.warning(err.msg)
      console.log(err.response.data)
    
    });
};



