// Create Profile
export const createProfile = (userID, fd, history) => dispatch => {
    // Headers
    const config = {
      headers: {
        // 'Accept': 'application/json',
        "x-auth-token": localStorage.getItem("token"),
        'Content-Type': 'application/json',
      }
    };console.log(fd.get("profilePicture"))
  new AWN().async(

    axios.put(`/api/signupuser/user/${userID}/profile`, fd, config)
    .then(res => {
      dispatch({
        type: USERS_LOADED,
        payload: res.data
      })
      // console.log(res.data)


      setTimeout(() => {
        window.location.href = `/dashboard/profile` 
      }, 3000);

      
           
    }
      ),
       "profile was updated successfully. Redirecting..."
  )


      .catch(err => {
        dispatch(
          returnErrors(err.response.data, err.response.status, 'UPDATE PROFILE FAILED')
        );
        // console.log(err)
        dispatch({
          type: REGISTER_FAIL


          
        });
      });
  };



export const getProfile = () => dispatch => {
  // Headers
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      'Content-Type': 'application/json',
    }
  };

  dispatch({
    type: PROFILE_LOADING
  })


    axios.get(`api/signupuser/user/profile`, config)


    .then(res => {
      dispatch({
        type: PROFILE_LOADED,
        payload: res.data
      })
      // console.log(res.data)
      // window.location.href = "http://localhost:3000/dashboard"        
    }
      )
    .catch(err => {
        notifier.warning("Your Session Expired. Please Login Again")
      console.log(err.response.data)
      dispatch(
        returnErrors(err.response.data, err.response.status, 'PROFILE GET FAILED')
      );
      // console.log(err.response.data)
      dispatch({
        type: REGISTER_FAIL
      });
    });
};











