import React, { Component } from 'react';
import {connect} from 'react-redux'
import { 
  withRouter,

 } from "react-router-dom"
import {setWriter} from "../../../action/writerActions"

class Volunteer extends Component {
    handleSubmit = async(e) => {
        e.preventDefault()
        // console.log("HELLO WORLD")
        await this.props.setWriter()
       
    }
    render() {
        return (
            <>
            <div class="container-fluid">
    <div class="col-lg-12 ">
        <div className="row">



            <div className="col-md-4 col-sm-12">
            <div class="card shadow mb-4">
                <div class="card-header py-3">
                <h6 class="m-0 font-weight-bold text-primary text-uppercase">
                     <center>
                        Click To Volunteer
                     </center>
                     </h6>
                </div>
                <div class="card-body">
                    <center>
                        {/* <form>

                        </form> */}
                    <button 
                    
                    onClick = {this.handleSubmit}
                    className="btn btn-primary" style={{
                        "backgroundColor": "#cf4edf",
                        "backgroundImage": "linear-gradient(180deg, #592277 10%, #224abe 100%)",
                       "backgroundSize": "cover"
                    }}>CLICK HERE!</button></center>
                </div>
        </div>
            </div>


      </div>
     </div>
</div>

</>

        );
    }
}


const mapStateToProps = state => ({
    error: state.error,
    writer:state.writer
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{setWriter})(
    withRouter(Volunteer)
  );














