import Fragment from "../../../hoc/Fragment";
import React, { Component } from "react";
import {
  Link,
  withRouter,
  Redirect,
  BrowserRouter as Router,
} from "react-router-dom";
import { connect } from "react-redux";

import {
    EditorState,
    convertFromHTML as convertFromHTMLClassic,
  } from 'draft-js';
  
  import DraftJsRichEditorExample from "./DraftJsRichEditorExample"
import { stateToHTML } from "draft-js-export-html";
  
import {getSingleCampaign,addFaq} from '../../../action/campaignActions'

import store from "../../../store";
import Pusher from "pusher-js";

class AddFaq extends Component {
  componentDidMount(){
      if(this.props.match.params.slug){
        //   console.log("state variables ", this.props.state)

          store.dispatch(getSingleCampaign(this.props.match.params.slug))

         //  console.log("CAMPAIGN:",this.props.profile)
       }
  }


  state = {

    editorState: EditorState.createEmpty(),
  }

  handleChange  = editorState => {
    this.setState({
      editorState,
      editorContentHtml: stateToHTML(editorState.getCurrentContent())
    
    });
  };


  handleSubmit= async e => {
    e.preventDefault()
    const {user}= this.props.auth
    
    // const fd= new FormData()
    

          var description;
          if(this.state.editorContentHtml){
             description =this.state.editorContentHtml
          } else{
             description =stateToHTML(this.state.editorState.getCurrentContent())
          }
    
          const det={
            

            question : this.question.value,
            answer : description,
            project : this.props.campaign.singleCampaign._id
          } 

          // console.log("det ", det)

          this.setState({
            editorState: EditorState.createEmpty()
          })

          this.question.value = ""



          // const id =this.props.location.state.slug

    await this.props.addFaq(this.props.match.params.slug,det)

  }
  


  render() {
    const { badges } = this.props.badge;
    // console.log(badge)
    return (
      <div class="container-fluid">
        <div class="col-lg-12">
          <center>
            <form
              action=""
              onSubmit={this.handleSubmit}
              enctype="multipart/form-data"
            >
              {/* PROJECT NAME */}

              {/* <div className="row"> */}
            
                <div className="col-md-12">
                  <label for="project_name">QUESTION</label>
                  <br />
                  <input
                    type="text"
                    name="question"
                    id=""
                    className="form-control mb-3"
                    placeholder="Enter Frequently Asked Question"
                    ref={(input) => {
                      this.question = input;
                    }}
                    required
                  />
                </div>
              {/* </div> */}

              <div class="mt-4">
                <label for="project_description">YOUR ANSWER</label>
                <br />

                <DraftJsRichEditorExample
                  className="DraftEditor-root"
                  editorState={this.state.editorState}
                  onChange={this.handleChange}
                  placeholder="Enter The Answer ....."
                />
              </div>
    
              <hr className="hr" />
              <input
                className="btn btn-primary  btn-block"
                type="submit"
                value="ADD"
              />

              <br>
              
              
              </br>
                  <p>Total Number of FAQs : {this.props.campaign.singleCampaign && this.props.campaign.singleCampaign.faq.length}</p>
            </form>

            {/* <div class="card shadow mb-4">
                     <div class="card-header py-3">
                     <h6 class="m-0 font-weight-bold text-primary"> SEEMS YOUR SESSION HAS EXPIRED!!</h6>
                     </div>
                     <div class="card-body">
                     <Link to={`/login`} class="btn btn-facebook btn-block">
                     CLICK TO LOGIN !!
                 </Link>
                     </div>
                 </div>
                  */}
          </center>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  error: state.error,
  badge: state.badge,
  campaign : state.campaign
})

// export default ProjectForm
export default connect(mapStateToProps, {getSingleCampaign,addFaq})(withRouter(AddFaq));
