import React, { Component } from 'react';

import Modal from "react-awesome-modal";
import ImageUploader from 'react-images-upload';

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"


import Lottie from "react-lottie";

import DraftJsRichEditorExample from "./DraftJsRichEditorExample"
import { stateToHTML } from "draft-js-export-html";


import * as done from '../../../../loaders/done.json'

// import JSONTree from 'react-json-tree';
import {
  EditorState,
  ContentState,
  convertFromHTML as convertFromHTMLClassic,
} from 'draft-js';


import {getBlogCategories} from "../../../../action/blogAction"
import {editWriterPost, getWriterPost} from "../../../../action/writerActions"
import store from '../../../../store';


const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: done.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class EditPost extends Component {
    componentDidMount(){
        this.props.getBlogCategories()
        if(this.props.match.params.slug){
            this.props.getWriterPost(this.props.match.params.slug)
             let editorState;
             const blocksFromHTML = convertFromHTMLClassic(this.props.location.state.content);
             const contentState = ContentState.createFromBlockArray(blocksFromHTML);
             editorState = EditorState.createWithContent(contentState);            
               this.setState({
                 editorState:editorState,
               })
         }

    }
/** STATE */
    state = {
        title:null,
        content:null,
        image:[],
        useCryptoValue:false,
        submit : "submit",
        visible : false,
        data: '',
        text:'',
        theme: 'snow',
        editorState: EditorState.createEmpty(),
      }
/** STATE */
/** CLOSE MODAL */
    closeModal() {
      this.setState({
        visible: false
      });

      window.location.href ="/admin/dashboard"
    }
/** CLOSE MODAL */
    // IMAGE SLIDE 1 
    onDrop1 = (picture) =>{
      this.setState({
        image: this.state.image.concat(picture),
      })
  }

  


      handleChange  = editorState => {
        this.setState({
          editorState,
          editorContentHtml: stateToHTML(editorState.getCurrentContent())
        
        });
      };

      componentDidUpdate(prevProps){


        console.log("prev props ", prevProps)
  
        if(this.props.blog.type !== prevProps.blog.type){
          if(this.props.blog.type === "WRITER_EDITED_POST"){
            this.setState({
              visible : true
            })
          }
        }
      }
    
      
      handleSubmit= async e => {
        e.preventDefault()
        this.setState({submit : "creating post..."})
        const {user}= this.props.auth
  
    
        const fd= new FormData()
              fd.append("image",this.state.image[0])
              fd.append("content", this.state.editorContentHtml)
              fd.append("title", this.title.value)
              fd.append("category",this.category.value)
              this.props.editWriterPost(this.props.match.params.slug,fd)   
      }
  
    render() {

        const {blogcategories} = this.props.blog
        console.log("CATEGORIES:",blogcategories)
        const {singlePost} = this.props.writer
        // defaultValue= {dashboardsingleCampaign.category}

        return(
            <>
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">CREATE POST</h6>
            </div>
            <div class="card-body">
            <form action="" onSubmit={this.handleSubmit} encType="multipart/form-data">



    {/* PROJECT NAME */}

    <div className="row">

    <div className="col-md-6">
        <label htmlFor="project_name">POST TITLE</label><br />
            <input 
                type="text" 
                name="title" 
                id=""
                className="form-control mb-3" 
                placeholder="enter post title"
                ref = {(input)=> {this.title=input}}
                required
                defaultValue={singlePost && singlePost.title}
            />
  </div>
  <div className="col-md-6">
        <label htmlFor="project_name">POST CATEGORY</label><br />
        <select
className="custom-select mb-5"
  required 
  defaultValue={singlePost && singlePost.category}
    name="category" id=""  ref={(input) => this.category = input}>
      {blogcategories && blogcategories.map((category,key)=>{
        return(
          <option value={category._id} key ={key}>{category.category_name}</option>
        )
      })}
 

</select>
  </div>

    </div>


<div class="mt-4">
<div className="col-md-12  mb-3">
        <label htmlFor="text">UPLOAD AN IMAGE: JPG OR PNG</label>

        <ImageUploader
                        withIcon={true}
                        buttonText='Choose images'
                        onChange={this.onDrop1}
                        imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                        withPreview = {true}
                        maxFileSize={5242880}

                        singleImage = {true}
                        withPreview = {true}
                    />

</div>

  </div>    
  <div class="mt-4">
  <label htmlFor="project_description">YOUR POST</label><br />
 
  <DraftJsRichEditorExample
    className="DraftEditor-root"
    editorState={this.state.editorState}
    defaultEditorState={this.state.editorState}
    onChange={this.handleChange}
    defaultValue={singlePost && singlePost.content}

  />

  </div>
  <div>
  </div>
<hr />

  <hr className="hr" />
  <input className="btn btn-primary  btn-block" type="POST" value={this.state.submit} />
</form>

            </div>


            <Modal
                    visible={this.state.visible}
                    width="60%"
                    height="80%"
                    effect="fadeInLeft"
                    onClickAway={() => {
                      this.closeModal();
                      // this.setState({ msg: "" });
                    }}
                  >
                    (
                      <div className = "container">
                   
                        <center>
                          <h2>Post Created Successfully</h2>
                        

                          <Lottie
                        options={defaultOptions}
                        height={"50%"}
                        width={"50%"}
                      />
<br></br>
<button
                            className="btn btn-danger"
                            value="Close"
                            onClick={() => this.closeModal()}
                          >
                            Close
                          </button>
                        </center>

                    
                      </div>
                
                  </Modal>
          </div>

            </>
        )
    
    }
}


const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    blog:state.blog,
    writer:state.writer
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{
      getBlogCategories,
      editWriterPost,
      getWriterPost
  })(
    withRouter(EditPost)
  );

