import React from 'react';
import Fragment from "../../../hoc/Fragment"
import { 
  Link,
  withRouter,
 } from "react-router-dom"
 import store from '../../../store'
 
import * as CampaingLoading from '../../../loaders/loading.json'
import {
  getProjects,
  flagProject,
  deleteProject,
  getProjectDetail,
  getTotalProjects,
  getAmountRaised,
  verifyCampaign,
  publishCampaign
} from  "../../../action/adminAction"

import {connect} from 'react-redux'
import "../assets/css/dataTables.bootstrap4.min.css"
import Lottie from "react-lottie";

import AWN from "awesome-notifications";
let notifier = new AWN();

const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: CampaingLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class Projects extends React.Component{
  componentDidMount() {
    // console.log("IN THE PAGE RN");

      store.dispatch(getProjects());
      store.dispatch(getTotalProjects());
      store.dispatch(getAmountRaised());
      
  }

 state = {
   loading : false
 }

  render(){
    const {isAuthenticated}= this.props.auth
    const {amountRaised, adminProjects,adminProjectDetail,adminProjectsLength}=this.props.admin
    const admin =this.props.auth.user
    var raised;
    if(amountRaised){
      // console.log(amountRaised['0'])
      raised=amountRaised['0'] ? amountRaised['0'].count : 0

    }

//     let onOk = () => {notifier.info('You pressed OK')};
// let onCancel = () => {notifier.info('You pressed Cancel')};
// notifier.confirm(
//   'Are you sure?',
//   onOk,
//   onCancel,
//   {
//     labels: {
//       confirm: 'Dangerous action'
//     }
//   }
// )

      return(
        <Fragment>
      <div class="card shadow mb-4">
              <div class="card-header py-3">
      <h6 class="m-0 font-weight-bold text-primary">{adminProjectsLength} CAMPAIGNS   ₦ {raised}Raised</h6>
              </div>
              <div class="card-body">
  
                {this.state.loading ?    
                <div className = "container"> 
                        <Lottie
                          options={defaultOptions}
                          height={"100%"}
                          width={"100%"}
                        />
                        </div>
                      :   <div class="table-responsive">
                        <table class="table"  width="100%" cellspacing="0">
                          <thead class="thead-dark">
                            <tr>
                            <th>Project Name</th>
                        <th>Email</th>
                        <th>Project Created</th>
                        <th>Project Goal</th>
                        <th>Amount Raised</th>
                        <th>Status</th>
                        <th>Edit</th>
                        <th>Flag</th>
                        <th>Delete</th>      
                        <th>Publish </th>
                        <th>Verified</th>   
                        <th>Published</th>   

                                
                            </tr>
                          </thead>
                          <tbody>
                          {
                        
                        adminProjects?
    
                     
    
    
                        adminProjects.map((project, key)=> {
                        return(
  
                            <tr key={key}>
                            <td>
                              <Link  to={{
                                pathname: `${`/admin/dashboard/${project.slug}/project`}`,
                                state: {
                                  content: `${project.project_deadline}`,
                                  adminID:`${admin.id}`
                                  

                                }
                              }}
                              >
                                {project.project_name}
                              </Link>
                              </td>
                            <td>{project.email}</td>
                            <td className="process">
                            {project.date_created

                                    
                                    }
                              </td>                      
                              <td>₦ {project.project_goal}</td> 
                              <td>₦ {project.project_current_balance}</td>
                              <td>{project.status == "publish" ? "published" : project.status}</td>
                              <td>
                                <Link
                                to={{
                                  pathname: `${`/admin/dashboard/${project.slug}/edit-campaign`}`,
                                  state: {
                                    content: `${project.description}`
                                  }
                                }}
                                
                                >
                                <i className="fa fa-flag"></i>
                                </Link>
                                </td>

                            <td>
  
            <Link
            onClick={(e)=> {
              e.preventDefault()

               const det ={

                projectID:project.slug

                 }
                this.props.flagProject(det)
            }}
            >
              <i className="fa fa-flag"></i>
            </Link>
                            </td>
                            <td>
                            <Link 
                              href="#"
                               onClick={(e)=> {
                                 e.preventDefault()
                                  const det ={

                                      projectID:project.slug
                  
                                    }
                                   this.props.deleteProject(det)
                               }}
                              >
                              <i className="fa fa-times"></i></Link>
                            
                            </td>

                            <td>
                            {project.status == "publish" ?
                            
                            <><button 
                            className="btn btn-primary"
                            disabled>PUBLISHED</button></>
                            : <button 
                            
                            style={{
                              backgroundColor: "#cf4edf",
                              backgroundImage: "linear-gradient(180deg, #592277 10%, #224abe 100%)",
                              backgroundSize: "cover"
                            
                            }}
                            
                            className="btn btn-primary  "
                            onClick = {()=>{
                                       let onOk = () => {
                                        //  console.log("verifying campaign")

                                          const det ={

                                              slug:project.slug
                          
                                            }
                                            // console.log(project.slug)
                                        store.dispatch(publishCampaign(project.slug))
                                       };
                                       let onCancel = () => {notifier.info('You pressed Cancel')};
                                        notifier.confirm(
                                          "Are You Sure You Want To Publish This Campaign?",
                                          onOk,
                                          onCancel)
                                      }}>Publish</button>}

                            </td>


                                    <td>
                                      {project.verified ? "Verified" : <button className ="btn btn-primary" onClick = {()=>{
                                       let onOk = () => {
                                        //  console.log("Verif campaign")

                                          const det ={

                                              slug:project.slug
                          
                                            }
                                            // console.log(project.slug)
                                        store.dispatch(verifyCampaign(project.slug))
                                       };
                                       let onCancel = () => {notifier.info('You pressed Cancel')};
                                        notifier.confirm(
                                          "Are You Sure You Want To Verify This Campaign?",
                                          onOk,
                                          onCancel)
                                      }}>Verified</button>}
                                    </td>

                                     {/* Verify Campaign End */}
        
                            <td>
                        
                            </td>
                            </tr>    
        
                        )}) : <tr>NO PROJECT</tr>}
                        
                        </tbody>
        
                        </table>
        
                      </div>
        }
              
              </div>
            </div>
       </Fragment>
    )



   
  }
    
}


const mapStateToProps = state => ({
  auth: state.auth,
  error: state.error,
  admin:state.admin
});

// export default ProjectForm
export default connect(mapStateToProps,{
  getProjects,
  deleteProject,
  flagProject,
  verifyCampaign,
  publishCampaign
})(
  withRouter(Projects)
);


