import React from 'react';
import Fragment from "../../../hoc/Fragment"

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {updateCampaign,getSingleDashboardCampaign,getAllCategories} from "../../../action/campaignActions"
import store from "../../../store"


import DraftJsRichEditorExample from "./DraftJsRichEditorExample"
import { stateToHTML } from "draft-js-export-html";

import {
  EditorState,
  ContentState,
   Editor,
    RichUtils, 
    convertToRaw, 
  convertFromHTML as convertFromHTMLClassic,
} from 'draft-js';



class  ProjectEditForm extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      project_name:"",
      project_description:null,
      image_slide_1:[],
      image_slide_2:[],
      image_slide_3:[],
      pitch_deck:null,
      fb_url:null,
      linkedin:null,
      inst_url:null,
      video_url:null,
      project_deadline:null,
      project_goal:null,  
      useCryptoValue:false,
      submit : "submit",
      visible : false,
      data: '',
      text:'',
      theme: 'snow',
      // editorState: EditorState.createEmpty(),
        useCryptoValue:false
    }
    // console.log("PROJECT slug",this.props.dashboard.dashboardsingleCampaign.slug)
  }

    // state
    componentDidMount(){
        // console.log("IN THE PAGE RN")
        this.props.getAllCategories()
        // console.log(this.props)
        if(this.props.match.params.id){
          //  console.log(this.props.match.params.id)
           store.dispatch(getSingleDashboardCampaign(this.props.match.params.id))
            let editorState;
            const blocksFromHTML = convertFromHTMLClassic(this.props.location.state.content);
            const contentState = ContentState.createFromBlockArray(blocksFromHTML);
            editorState = EditorState.createWithContent(contentState);            
              this.setState({
                editorState:editorState,
              })
        }
     }


     /** IMAGE UPLOADS */
         // IMAGE SLIDE 1 
            onDrop1 = (picture) =>{
              this.setState({
                image_slide_1: this.state.image_slide_1.concat(picture),
              })
          }

          // IMAGE SLIDE 2
          onDrop2 = (picture) =>{
            this.setState({
              image_slide_2: this.state.image_slide_2.concat(picture),
            })
        }


        // IMAGE SLIDE 3
        onDrop3 = (picture) =>{
          this.setState({
            image_slide_3: this.state.image_slide_3.concat(picture),
          })
        }


     /** END IMAGE UPLOAD */
     onProjectNameChange = e => {
        this.setState({ project_name: e.target.value });
        e.target.value = this.state.project_name
        // console.log(this.state.project_name)
      };


truncate= (str) =>{
  return str.length > 10 ? str.substring(0, 30) + "..." : str;
}


handleChange  = editorState => {
  this.setState({
    editorState,
    editorContentHtml: stateToHTML(editorState.getCurrentContent())
  
  });
};




     handleSubmit= async e => {
      e.preventDefault()
      const {user}= this.props.auth
      
      const fd= new FormData()
      

            var description;
            if(this.state.editorContentHtml){
               description =this.state.editorContentHtml
            } else{
               description =stateToHTML(this.state.editorState.getCurrentContent())
            }
      
            const det={
              userID:user.id,
              project_name:this.project_name.value,
              project_description:description, 
              fb_url:this.fb_url.value,
              linkedin:this.linkedin.value,
              inst_url:this.inst_url.value,
              video_url:this.video_url.value,
              email:user.email,
              category:this.category.value,
              id : this.props.dashboard.dashboardsingleCampaign.slug
            } 

            console.log("det ", det)
            // const id =this.props.location.state.slug

      await this.props.updateCampaign(det)

    }
    
    render(){
      
      const {categories} = this.props.campaign
        const {dashboardsingleCampaign} = this.props.dashboard
        // console.log("data i need " ,dashboardsingleCampaign)
        // console.log("RENDER", camp)
        return(
            <Fragment>
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">EDIT CAMPAIGNS</h6>
            </div>
            <div class="card-body">
            {this.props.error.status === 400 ? 
                                   <div class="alert alert-warning alert-dismissible fade show" role="alert">
                                   <strong>
                                              {this.props.error.msg.msg} !!                        
                                   </strong> 
                                   <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                     <span aria-hidden="true">&times;</span>
                                   </button>
                                 </div>
                           
                           
              : null}
            <form action="" onSubmit={this.handleSubmit} enctype="multipart/form-data">



    {/* PROJECT NAME */}

<div className="row">
        {/* PROJECT CATEGORY */}
    <div className="col-md-6">
        <label for="option" >Category:
          <span style={{"color":"purple"}} >
          </span>
        </label>
        <select
            className="custom-select mb-5"
              required 
              name="" 
              id=""  
              defaultValue= {dashboardsingleCampaign ? dashboardsingleCampaign.category["_id"]:null}
              ref={(input) => this.category = input}
              >        

               {categories.map((category,key)=>{
        return(
          <option value={category._id} key ={key}>{category.category_name}</option>
        )
      })}    

        </select>
    </div> 
{/* PROJECT NAME */}
  <div className="col-md-6">
    <label for="project_name">CAMPAIGN NAME: </label><br />

      <input 
          type="text" 
          name="project_name"
          id=""
          defaultValue={dashboardsingleCampaign.project_name}
          className="form-control mb-3" 
          placeholder="Enter Campaign Name"
          ref = {(input)=> {this.project_name=input}}
          required
        />
  </div>
</div>

{/* PROJECT DESCRIPTION */}
<div class="mt-4">
    <label for="project_description">YOUR CAMPAIGN STORY</label><br />
      {this.state.editorState ? 
      
    <DraftJsRichEditorExample
    className="DraftEditor-root"
    editorState={this.state.editorState}
    defaultEditorState={this.state.editorState}
    onChange={this.handleChange}
    defaultValue={dashboardsingleCampaign.project_description}

  />
      :null}
</div>
<hr />
{/* PROJECT PITCH DECK */}




<div className="row mt-4">


  <div className="col-md-6">
      <label for="fb_url">
      FACEBOOK PAGE LINK      </label>
      <input 
        type="url"
        className="form-control mb-5" 
        name="fb_url" 
        id=""
        defaultValue={dashboardsingleCampaign.fb_url}
        ref = {(input)=> {this.fb_url=input}}  
        placeholder="ENTER YOUR FACEBOOK PAGE LINK" 
        required
        />
</div>
<div className="col-md-6">
      
      <label for="linkedin">
      TWITTER PAGE LINK:  </label>
        <input
          className="form-control mb-5" 
          type="url" 
          name="linkedin" 
          id="linkedin"
          defaultValue={dashboardsingleCampaign.linkedin}
          required 
          ref = {(input)=> {this.linkedin=input}}
          placeholder="ENTER YOUR LINKEDIN PROFILE LINK" 
        />
      </div>
    
</div>

<div className="row mt-1">



  <div className="col-md-6">
      <label for="inst_url">
      INSTAGRAM PAGE LINK    </label>
    <input 
    required
    className="form-control mb-5"
    type="url" 
    name="inst_url" 
    id="inst_url" 
    defaultValue={dashboardsingleCampaign.inst_url}
    placeholder="ENTER YOUR INSTAGRAM PAGE LINK" 
    ref = {(input)=> {this.inst_url=input}}
    />
</div>
    

<div className="col-md-6">
  <label for="video_url">
  VIDEO LINK  </label>
  <input 
    type="url" 
    name="video_url" 
    className="form-control"
    id="" 
    defaultValue={dashboardsingleCampaign.video_url}
    ref = {(input)=> {this.video_url=input}}
    placeholder="ENTER YOUTUBE LINK" 
  />
</div>
</div>



  <hr className="hr" />
  <input 
  
  style={{
    backgroundColor: "#cf4edf",
    backgroundImage: "linear-gradient(180deg, #592277 10%, #224abe 100%)",
    backgroundSize: "cover"

  }}
  className="btn btn-primary  btn-block" type="SAVE" value="submit" />
</form>

            </div>
          </div>

            </Fragment>
        )
    
    }
}


const mapStateToProps = state => ({
  auth: state.auth,
  error: state.error,
  campaign:state.campaign,
  dashboard:state.dashboard
});

// export default ProjectForm
export default connect(mapStateToProps,{updateCampaign,getAllCategories})(
  withRouter(ProjectEditForm)
);