export const deleteWriterPost = (slug) => dispatch => {
  const config = {
    headers: {
      // 'Accept': 'application/json',
      "x-auth-token": localStorage.getItem("token"),
      "Content-Type": "application/json"
    }
  };


    axios
      .delete(`/api/blog/writer-delete-post/${slug}`, config)
      .then(res =>{
        notifier.success("Deleted Successfully")


        setTimeout(() => {
          window.location.href= "/dashboard"
        }, 3000);      
      }
      )
      .catch(err => {
        console.log(err.response.data)
        notifier.warning("DELETE FAILED, CONTACT ADMINISTRATOR OR EMAIL CUSTOMERCARE@ELESARR.COM")
      });

  };

