import React, { Component } from 'react';

import {
    getWriterPosts,
    deleteWriterPost

} from  "../../../../action/writerActions"
import {connect} from 'react-redux'
import Lottie from "react-lottie";

import { 
    Link,
    withRouter,
   } from "react-router-dom"
import store from '../../../../store'
import * as CampaingLoading from '../../../../loaders/loading.json'
const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: CampaingLoading.default,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
class Posts extends Component {
    componentDidMount(){
        this.props.getWriterPosts()
    }
    state = {
        loading : false
      }
     
    render() {
            const {isAuthenticated}= this.props.auth
            const {posts}=this.props.writer
            
           
              return(
                <>
                  <div class="d-sm-flex align-items-center justify-content-between mb-4">
                  <h1 class="h3 mb-0 text-gray-800">Posts</h1>
                  <Link 
                    to="/dashboard/writer/add-post" 
                    class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm">
                        <i class="fa fa-comment-plus  fa-sm text-white-50"></i> Add Post</Link>
                </div>

              <div class="card shadow mb-4">
                      <div class="card-header py-3">
              {/* <h6 class="m-0 font-weight-bold text-primary">{adminPostLength} POSTS</h6> */}
                      </div>
                      <div class="card-body">
          
                        {this.state.loading ?    
                        <div className = "container"> 
                                <Lottie
                                  options={defaultOptions}
                                  height={"100%"}
                                  width={"100%"}
                                />
                                </div>
                              :   <div class="table-responsive">
                                <table class="table"  width="100%" cellspacing="0">
                                  <thead class="thead-dark">
                                    <tr>
                                    <th>Post Title</th>
                                <th>Post Image</th>
                                <th>Date Created</th>
                                <th>Status</th>
                                
                                <th>Edit</th>
                                <th>Delete</th>                      
                                    </tr>
                                  </thead>
                                  <tbody>
                                  {posts ? posts.map((post, key)=> {
                                return(
          
                                    <tr key={key}>
                                    <td>
                                      <Link 
                                       
                                      >
                                        {post.title}
                                      </Link>
                                      </td>
                                    <td>
                                    <img width="100" height="100" src={post.image}/>
                                        
                                        </td>
                                    <td className="process">
                                    {new Intl.DateTimeFormat("en-GB", {
                                                year: "numeric",
                                                month: "long",
                                                day: "2-digit"
                                            }).format(post.date_created)}
                                      </td>                      
                                      <td>{post.status}</td> 
                                     
                                    <td>
          
                    <Link 
                                   to={{
                                              pathname: `/dashboard/writer/${post.slug}/edit-post`,
                                              state: {
                                                content: `${post.content}`,
                                                
                                              },
                                            }}
                     
                    >
                      <i className="fa fa-edit"></i>
                    </Link>
                                    </td>
                                    <td>
                                    <Link 
                                      href="#"
                                       onClick={(e)=> {
                                         e.preventDefault()
                                        
                                           this.props.deleteWriterPost(post.slug)
                                       }}
                                      >
                                      <i className="fa fa-trash"></i></Link>
                                    
                                    </td>
                
                                    <td>
                                
                                    </td>
                                    </tr>    
                
                                )}) 
                                
                                : 
                                
                                <tr>
                                  <center>
                                  <h1>NO POSTS</h1>
                                  </center>
                                  </tr>
                                  
                                  }
                                
                                </tbody>
                
                                </table>
                
                              </div>
                }
                      
                      </div>
                    </div>
               </>
            )
    }
}




const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    writer:state.writer
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{
    getWriterPosts,
    deleteWriterPost
  })(
    withRouter(Posts)
  );
  
  