import React, { Component } from 'react';
import {getRecentCampaigns} from "../../../action/campaignActions"
import {connect} from 'react-redux'
import { 
    Link,
  withRouter,
 } from "react-router-dom"
 import moment from "moment";
import store from "../../../store"

class TrendingProjects extends Component {
    componentDidMount(){
        //  this.props.getAllCampaign()
        store.dispatch(getRecentCampaigns())
    
    
        // await sleep(3000)
        // console.log("CATEGORIES:",this.props.campaigns.recentcampaigns)
        // console.log("PROPS:",this.props.getAllCampaign)
    }


    render() {
  
      // console.log("campaign ", this.props.campaigns.recentcampaigns)

            return(
                <React.Fragment>
{/*====== EXPLORE PROJECTS PART START ======*/}
{/*====== EXPLORE PROJECTS PART START ======*/}
<section className="explore-projects-area explore-projects-2-area">
  <div className="container">
    <div className="row justify-content-center">
      <div className="col-lg-8">
        <div className="section-title text-center">
          <span>Businesses You Can Back</span>
          <h3 className="title">Explore Projects</h3>
        </div>
      </div>
    </div>
    <div className="row explore-project-active">

    {this.props.campaigns.recentcampaigns.map((campaigns, key)=> {
      // console.log("campaign ", campaigns)
      const dateInFuture =new Date(campaigns.project_deadline).getTime();
      const now = new Date().getTime();

      // const countdown = moment(then - now);
      var timeleft = dateInFuture - now;


      var days = Math.floor(timeleft / (1000 * 60 * 60 * 24));
      

      return(
        <div className="col-lg-4" key ={key}>
        <div className="explore-projects-item mt-30">
          <div className="explore-projects-thumb">
            <img src={campaigns.image_slide_2} style ={{height : "250px"}} alt="explore-projects" />
            <a href="#"><i className="fa fa-heart" /></a>
          </div>
          <div className="explore-projects-content">
            <div className="item d-flex align-items-center">
      <span>{campaigns.category["category_name"]}</span>
              <p><i className="fa fa-clock-o" />
                  { days <= 0  ?  " CAMPAIGN STOPPED!":  
                  
                `${days} day(s) left`
                  
                  }
                  </p>
            </div>
                <Link to={`/campaigns/${campaigns.slug}`}><h3 className="title">{campaigns.project_name}</h3></Link>
            <div className="projects-range">
              <div className="projects-range-content">
                <ul>
                  <li>Raised:</li>
                  <li>{Math.round((campaigns['project_current_balance']/campaigns['project_goal']) * 100)}%</li>
                </ul>
                <div className="percentage mt-0">
                                          <div className="progress">
                                              <div 
                                                  className="progress-bar color-8" 
                                                  role="progressbar" 
                                                  style={{
                                                          width:`${(campaigns['project_current_balance']/campaigns['project_goal']) * 100}%`, 
                                                          // ,
                                                          backgroundColor:"#5E26A6"
                                                      }}
                                                  aria-valuenow={campaigns['project_current_balance']} 
                                                  aria-valuemin="0" 
                                                  aria-valuemax={campaigns['project_goal']} >
          
                                                  </div>
                                          </div>
                                      </div> 
             
              </div>
            </div>
            <div className="projects-goal">
                <span>Goal: <span>{campaigns.project_goal}</span></span>
            </div>
          </div>
        </div>
       </div>
       
      )

     } )}
      </div>
  </div>
</section>
{/*====== EXPLORE PROJECTS PART ENDS ======*/}

{/*====== EXPLORE PROJECTS PART ENDS ======*/}
</React.Fragment>
       );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaigns:state.campaign
  });
  

  export default connect(mapStateToProps,{getRecentCampaigns})(
    withRouter(TrendingProjects)
  );





