import {
    ADDING_USER_NEWSLETTER,
    ADDED_USER_NEWSLETTER
    
 } from "../action/types"
 import axios from "axios"

 const initialState ={
     isLoading:false,
    msg:""
 }



 export default function(state =initialState, action){
    
    switch (action.type) {
        
            case ADDED_USER_NEWSLETTER:
                return{
                    ...state,
                    isLoading:false,
                    msg:"SUCCESSFULLY ADDED TO OUR NEWSLETTER LIST"
                }
        case ADDING_USER_NEWSLETTER:
            return {
                // ...state, 
                isLoading:true
            };

            
        default:
            return state;
    }
 }