import React, { Component } from "react";
import { getAllCampaign } from "../../../action/campaignActions";
import { connect } from "react-redux";

import TextTruncate from "react-text-truncate"; // recommend

import { Link, Route, Switch, BrowserRouter as Router } from "react-router-dom";
import { withRouter } from "react-router-dom";
import store from "../../../store";
import Fragment from "../../../hoc/Fragment";
import Lottie from "react-lottie";

import * as CampaingLoading from "../../../loaders/prettyLoading.json";

import * as NotFound from "../../../loaders/notFound.json";
import moment from "moment";
import Header from "../../home/components/Header";

// remove this later

const id = 1;
const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: CampaingLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

const defaultOptions2 = {
  loop: true,
  autoplay: true,
  animationData: NotFound.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class CampaignCategory extends Component {
  async componentDidMount() {
    // console.log("In the page i need ....")
    window.scrollTo(0, 0);
    this.setState({
      loading: true,
    });
    //  this.props.getAllCampaign()
    await store.dispatch(getAllCampaign());
    this.setState({
      loading: false,
    });
    function sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    }

    // await sleep(3000)
    this.setState({
      loading: true,
    });

    setTimeout(() => {
      this.setState({
        loading: false,
      });
    }, 2000);
  }

  state = {
    loading: false,
  };

  render() {
    var { camp } = this.props.campaign;
    // console.log("slug ", this.props.match.params.slug);

    if (camp) {
      // console.log("initial camp ,", camp);
      camp = camp.filter(
        (campaign) => campaign.category.slug == this.props.match.params.slug
      );
      // console.log("new camp details ", camp);
    }

    return (
      <Fragment>
        {this.state.loading ? (
          <div className="container-fluid" style={{ marginTop: "15%" }}>
            <Lottie options={defaultOptions} height={"50%"} width={"50%"} />
          </div>
        ) : (
          <Fragment>
            <Header />
            {/*====== HEADER PART ENDS ======*/}
            {/*====== PAGE TITLE PART START ======*/}
            <section
              className="page-title-area bg_cover"
              style={{
                backgroundImage:
                  // IMAGES BASED ON CAMPAIGN TYPE
                  this.props.match.params.slug.toLowerCase() ==
                  "Technology-and-Innovation".toLowerCase()
                    ? `url(https://images.pexels.com/photos/356056/pexels-photo-356056.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)`
                    : this.props.match.params.slug.toLowerCase() ==
                      "Charity".toLowerCase()
                    ? `url(https://images.pexels.com/photos/99820/pexels-photo-99820.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)`
                    : this.props.match.params.slug.toLowerCase() ==
                      "Education".toLowerCase()
                    ? `url(https://images.pexels.com/photos/159711/books-bookstore-book-reading-159711.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500)`
                    : this.props.match.params.slug.toLowerCase() ==
                      "Creatives".toLowerCase()
                    ? `url(https://images.pexels.com/photos/6469/red-hands-woman-creative.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)`
                    : this.props.match.params.slug.toLowerCase() ==
                      "agriculture".toLowerCase()
                    ? `url(https://images.pexels.com/photos/2284170/pexels-photo-2284170.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)`
                    : // :this.props.match.params.slug.toLowerCase() ==
                      // "entrepreneurship".toLowerCase() ?
                      `url(https://images.pexels.com/photos/356043/pexels-photo-356043.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)`,
              }}
            >
              <div className="container">
                <div className="row">
                  <div className="col-lg-12">
                    <div className="page-title-content">
                      <h3 className="title">{this.props.match.params.slug && this.props.match.params.slug} category</h3>
                      <nav aria-label="breadcrumb">
                        <ol className="breadcrumb">
                          <li className="breadcrumb-item">
                            <a href="/">Home</a>
                          </li>
                          <li
                            className="breadcrumb-item active"
                            aria-current="page"
                          >
                           <a href="/campaigns">Explore</a>
                          </li>
                        </ol>
                      </nav>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            {/*====== PAGE TITLE PART ENDS ======*/}
            {/*====== EXPLORE PART START ======*/}
            {camp && camp.length != 0 ? (
              <section className="explore-projects-3-area explore-v2-page pt-90 pb-120">
                <div className="container">
                  <div className="explore-margin">
                    <div className="row justify-content-center">
                      {camp &&
                        camp.map((campaign, key) => {
                          return (
                            // console.log("length ", camp.length),
                            <div
                              className={
                                camp.length == 1
                                  ? "col-lg-8 col-md-8 col-sm-12"
                                  : "col-lg-6 col-md-6 col-sm-9"
                              }
                              key={key}
                            >
                              <div className="explore-projects-item mt-30">
                                <div className="icon">
                                  <a href="#">
                                    <i className="fa fa-heart" />
                                  </a>
                                </div>
                                <img
                                  src={campaign.image_slide_2}
                                  style={{ height: "500px", width: "537px" }}
                                  alt=""
                                />
                                <div className="explore-projects-content">
                                  <div className="item d-flex align-items-center">
                                    <span>
                                      {campaign.category &&
                                        campaign.category["category_name"]}
                                    </span>
                                    <p>
                                      <i className="fa fa-clock-o" />{" "}
                                      {moment(campaign.date_created).fromNow()}
                                    </p>
                                  </div>
                                  <a href={`/campaigns/${campaign.slug}`}>
                                    <h3 className="title">
                                      {campaign.project_name}
                                    </h3>
                                  </a>
                                  <div className="projects-range">
                                    <div className="projects-range">
                                      <div className="projects-range-content">
                                        <ul>
                                          <li>Raised:</li>
                                          <li>
                                            {Math.round(
                                              (campaign[
                                                "project_current_balance"
                                              ] /
                                                campaign["project_goal"]) *
                                                100
                                            )}
                                            %
                                          </li>
                                        </ul>
                                        <div className="percentage mt-0">
                                          <div className="progress">
                                            <div
                                              className="progress-bar color-8"
                                              role="progressbar"
                                              style={{
                                                width: `${
                                                  (campaign[
                                                    "project_current_balance"
                                                  ] /
                                                    campaign["project_goal"]) *
                                                  100
                                                }%`,
                                                // ,
                                                backgroundColor: "#5E26A6",
                                              }}
                                              aria-valuenow={
                                                campaign[
                                                  "project_current_balance"
                                                ]
                                              }
                                              aria-valuemin="0"
                                              aria-valuemax={
                                                campaign["project_goal"]
                                              }
                                            ></div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          );
                        })}
                    </div>
                  </div>
                </div>
              </section>
            ) : (
              <div
                className="container-fluid d-flex flex-column"
                style={{ marginTop: "20px" }}
              >
                <center>
                  <strong>
                    <h2>Ooops!, No Campaigns Found With This Category</h2>
                  </strong>
                </center>
                <Lottie
                  options={defaultOptions2}
                  height={"80%"}
                  width={"50%"}
                />
              </div>
            )}
            {/*====== EXPLORE PART ENDS ======*/}

            {/*====== GO TO TOP PART START ======*/}
            <div className="go-top-area">
              <div className="go-top-wrap">
                <div className="go-top-btn-wrap">
                  <div className="go-top go-top-btn">
                    <i className="fa fa-angle-double-up" />
                    <i className="fa fa-angle-double-up" />
                  </div>
                </div>
              </div>
            </div>
            {/*====== GO TO TOP PART ENDS ======*/}
          </Fragment>
        )}
      </Fragment>
    );
  }
}

//  export default  ;

// export default

const mapStateToProps = (state) => ({
  auth: state.auth,
  error: state.error,
  campaign: state.campaign,
});
// export default ProjectForm
export default connect(mapStateToProps, { getAllCampaign })(
  withRouter(CampaignCategory)
);
