import Fragment from "../../../hoc/Fragment"
import React, { Component } from 'react';
import { 
  Link,
  withRouter,
  Redirect,
  BrowserRouter as Router } from "react-router-dom"
import {connect} from 'react-redux'
import {getUserBadge} from '../../../action/badgeAction'

import store from '../../../store'
import Pusher from 'pusher-js';



class Badges extends Component {
    componentDidMount(){
        if(this.props.match.params.email){
            // console.log(this.props.match.params.email)
           
            store.dispatch(getUserBadge(this.props.match.params.email))
           
           //  console.log("CAMPAIGN:",this.props.profile)
         }
    }
    render() {
        const {badges} = this.props.badge
        // console.log(badge)
        return (
            <div class="container-fluid">
                <div class="col-lg-12">
                    <div className="row">

                {badges ? 
                    <React.Fragment>


                        {badges.map((badge, key) => {
                            return(
                        <div className="col-md-4">
                        <div class="card shadow mb-4">
                            <div class="card-header py-3">
                            <h6 class="m-0 font-weight-bold text-primary text-uppercase">
                                 <center>
                                    {badge.status} BADGE!!
                                 </center>
                                 </h6>
                            </div>
                            <div class="card-body">
                            <img src={`${badge.badgeurl}`} />
                            </div>
                    </div>
                        </div>

                            )
                        })}
                    </React.Fragment>
                : null}

                 {/* <div class="card shadow mb-4">
                     <div class="card-header py-3">
                     <h6 class="m-0 font-weight-bold text-primary"> SEEMS YOUR SESSION HAS EXPIRED!!</h6>
                     </div>
                     <div class="card-body">
                     <Link to={`/login`} class="btn btn-facebook btn-block">
                     CLICK TO LOGIN !!
                 </Link>
                     </div>
                 </div>
                  */}
                  </div>
                 </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    badge:state.badge
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{getUserBadge})(
    withRouter(Badges)
  );
  
  
  